@import url(https://fonts.googleapis.com/css?family=Exo:400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.image-modal {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0,0,0,0.8);
  }
  
  .image-modal-content {
    position: relative;
    padding: 0;
    width: 90%;
    max-width: 1920px;
  }
  
  .image-modal-close {
    position: absolute;
    top: 10px;
    right: 25px;
    color: white;
    font-size: 35px;
    font-weight: bold;
    cursor: pointer;
  }
  
  .image-modal-close:hover,
  .image-modal-close:focus {
    color: #999;
    text-decoration: none;
    cursor: pointer;
  }
  
  .image-modal img {
    margin: auto;
    display: block;
    width: 100%;
    max-width: 1920px;
  }  
.ticker-loop {
  overflow: hidden;
  position: relative;
  width: 100%;
  background-color: rgba(255, 255, 255, 0); /* 背景色を設定 */
  padding: 10px 0; /* 上下の余白を追加 */
  margin-top: 10vh;
}

.ticker-content {
  display: inline-block;
  white-space: nowrap;
  animation: tickerAnimation 60s linear infinite;
}

.ticker-item {
  display: inline-block;
  margin-right: 50px; /* アイテム間のスペースを設定 */
  font-size: 48px; /* フォントサイズを設定 */
  color: #2172c3; /* テキスト色を設定 */
}

@keyframes tickerAnimation {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-50%); /* コンテンツの幅の半分を移動 */
  }
}

/* スマホ画面用のレスポンシブデザイン */
@media (max-width: 768px) {
  .ticker-loop {
    margin-top: 5vh;
    padding: 5px 0; /* スマホ画面用に上下の余白を調整 */
  }
}
* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

body {
    font-family: 'Exo', sans-serif;
}

.area {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #ffffff;
    background: -webkit-linear-gradient(to left, #ffffff, #ffffff);
    z-index: -1;
    overflow: hidden;
}

.circles {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.circles li {
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    background: rgba(0, 128, 255, 0.2);
    animation: animate 25s linear infinite;
    bottom: -150px;
}

.circles li:nth-child(1) {
    left: 25%;
    width: 80px;
    height: 80px;
    animation-delay: 0s;
}

.circles li:nth-child(2) {
    left: 10%;
    width: 20px;
    height: 20px;
    animation-delay: 2s;
    animation-duration: 12s;
}

.circles li:nth-child(3) {
    left: 70%;
    width: 20px;
    height: 20px;
    animation-delay: 4s;
}

.circles li:nth-child(4) {
    left: 40%;
    width: 60px;
    height: 60px;
    animation-delay: 0s;
    animation-duration: 18s;
}

.circles li:nth-child(5) {
    left: 65%;
    width: 20px;
    height: 20px;
    animation-delay: 0s;
}

.circles li:nth-child(6) {
    left: 75%;
    width: 110px;
    height: 110px;
    animation-delay: 3s;
}

.circles li:nth-child(7) {
    left: 35%;
    width: 150px;
    height: 150px;
    animation-delay: 7s;
}

.circles li:nth-child(8) {
    left: 50%;
    width: 25px;
    height: 25px;
    animation-delay: 15s;
    animation-duration: 45s;
}

.circles li:nth-child(9) {
    left: 20%;
    width: 15px;
    height: 15px;
    animation-delay: 2s;
    animation-duration: 35s;
}

.circles li:nth-child(10) {
    left: 85%;
    width: 150px;
    height: 150px;
    animation-delay: 0s;
    animation-duration: 11s;
}

@keyframes animate {
    0% {
        transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }
    100% {
        transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }
}

/* src/App.css */

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: auto;
  font-family: "Helvetica Neue", "Helvetica", "Hiragino Sans", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
}

.App {
  text-align: center;
  margin: 0;
  padding: 0;
  position: relative;
}

.hamburger-menu {
  position: fixed;
  top: 10px;
  right: 15px;
  z-index: 3;
  text-align: right;
  text-align: -webkit-right;
}

.hamburger-button {
  background: none;
  border: none;
  color: gray;
  font-size: 36px;
  cursor: pointer;
  text-align: right;
  text-align: -webkit-right;
}

.hamburger-button.white {
  color: white;
}

.menu-items {
  position: fixed;
  top: 0;
  right: 0;
  height: 100%;
  width: 30%;
  background-color: rgba(70, 130, 180, 0.8);
  transform: translateX(100%);
  transition: transform 0.3s ease-in-out;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 2;
}

.menu-items.show {
  transform: translateX(0);
}

.menu-item {
  margin: 15px 0;
}

.menu-items a {
  color: white;
  text-decoration: none;
  font-size: 18px;
  line-height: 2;
  margin: 10px 0;
}

.menu-items a:hover {
  text-decoration: underline;
}

/* スライドインアニメーション */
h2 {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 1.0s ease-out, transform 1.0s ease-out;
  padding-bottom: 3px; /* テキストと下線の距離を調整 */
  color: #2172c3;
  position: relative;
  display: inline-block;
  padding: 0 30px;
}

h2.slide-in {
  opacity: 1;
  transform: translateY(0);
}

h2:before, h2:after {
  content: '';
  position: absolute;
  top: 50%;
  display: inline-block;
  width: 20px;
  height: 1px;
  background-color: black;
}
h2:before {
  left:0;
}
h2:after {
  right: 0;
}

.video-section {
  position: relative;
  height: 100vh;
  width: 100%;
  overflow: hidden;
}

.background-video {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 90%;
  max-width: 720px; /* Original video width */
  height: auto;
  max-height: 1280px; /* Original video height */
  transform: translate(-50%, -50%);
  z-index: -1;
}

.scroll {
  display: inline-block;
  padding-top: 70px;
  position: relative;
}
.scroll::before {
  animation: scroll 3.5s infinite;
  border: solid #000;
  border-width: 0 0 1px 1px;
  content: "";
  display: inline-block;
  margin: auto;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  transform: rotate(-45deg);
  width: 20px;
  height: 20px;
}
@keyframes scroll {
  0% {
    transform: rotate(-45deg) translate(0, 0);
  }
  80% {
    transform: rotate(-45deg) translate(-30px, 30px);
  }
  0%, 80%, 100% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
}

.content-profile {
  position: relative;
}

.profile {
  position: relative;
  height: 90vh;
  width: 100%;
  overflow: hidden;
  margin-bottom: 0vh; /* セクション間のスペースを確保 */
}

.profile-video {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 90%;
  /* height: 100%; */
  max-height: 80vh; /* 動画の最大高さを画面の高さに設定 */
  object-fit: cover; /* 動画がセクション全体をカバーするように */
  transform: translate(-50%, -50%);
  z-index: -1; /* テキストを上に表示 */
  border-radius: 15px; /* 角を丸くする */
}

.text-overlay {
  position: absolute;
  bottom: 12%;
  left: 10%;
  color: #f1f1f1;
  background: rgba(0, 0, 0, 0.0);
  padding: 10px;
  border-radius: 8px;
  font-size: 1rem;
  text-align: left;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  z-index: 1; /* テキストを動画の上に表示 */
}

.content {
  position: relative;
  /* height: 70vh; */
  width: 100%;
  overflow: hidden;
}

.program {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin-bottom: 5vh; /* セクション間のスペースを確保 */
}

.program .content-box {
  max-width: 90%;
  margin: auto; /* 中央揃え */
  padding: 20px; /* 内側の余白を追加 */
  background-color: rgba(255, 255, 255, 0.7); /* 背景色と透明度を追加 */
  border-radius: 10px; /* 角を丸める */
}

.content-box p {
  font-size: 14px;
}

.image-cursor {
  cursor: pointer;
  max-width: 100%;
  height: auto;
}

.program-schedule {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 0vh; /* セクション間のスペースを確保 */
}

.program-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px 0;
}

.program-time {
  font-size: 15px;
}

.program-event {
  font-size: 16px;
}

.program-info {
  font-size: 14px;
}

.content-message {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.content-message .content-box {
  max-width: 90%;
  margin: auto; /* 中央揃え */
  padding: 20px; /* 内側の余白を追加 */
  background-color: rgba(255, 255, 255, 0.7); /* 背景色と透明度を追加 */
  border-radius: 10px; /* 角を丸める */
  margin-bottom: 5vh; /* セクション間のスペースを確保 */
}

.footer {
  background-color: #f1f1f1;
  padding: 10px 0;
  text-align: center;
}

.footer-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer-content p {
  margin: 0;
  color: #666;
  font-size: 10px;
}

/* スマホ画面用のレスポンシブデザイン */
@media (max-width: 768px) {
  .video-section {
    height: 95vh;
    width: 100%;
  }

  .background-video {
    width: 95%;
  }

  .profile-video {
    top: 50%;
    left: 50%;
    /* width: 90%; */
    /* height: 100%; */
  }
  
  .text-overlay {
    bottom: 14%;
    left: 12%;
    font-size: 0.8rem;
  }
}

